@import url(https://fonts.googleapis.com/css2?family=Tenor+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap);
:root{
  --btn-color: #59e;
  --background-color:#ddd;
  --nav-color: #cee3ff;
  --modal-color:white;
  --link-color: #46e;
  --cart-preview-color: #e3e3e3;
  --font-color: #222;
  --follow-btn-color: #e56;
  --danger-color: #b34;
  --logo-font: 'Fredericka the Great', cursive;
}

*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  font-weight: 800;
}

body {
  font-family: 'Tenor Sans', sans-serif;
  background-color: #ddd;
  background-color: var(--background-color);
  color: #222;
  color: var(--font-color);
}

.app {
  margin: 1em auto;
  max-width: 95%;
}



#overlay {
  background-color: #aaa;
  opacity: 0;
  position:fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: 200ms ease-in-out;
  z-index:-1;
}

#overlay.active {
  opacity: 0.4;
  z-index:9;
}



.btn-wrapper {
  margin-top: 30px;
}

.btn {
  background-color: #59e;
  background-color: var(--btn-color) ;
  box-shadow: 0 2px 2px #559;
  border-radius: 9999px;
  padding: 0.5em;
  width: 95%;
  color: white;
  text-align: center;
  cursor: pointer;
}

.btn:hover{
  opacity: 0.8;
  box-shadow: none;
}



img {
  max-width: 100vw;
  height: auto;
}

main {
  margin: 90px 0;
  
}

.screen-title {
  margin:1em;
}

a {
  text-decoration: none;
}

nav {
    margin:0 auto;
    padding: 0.5em;
    position: fixed;
    right: 0;
    left: 0;
    top: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 0 2px 3px #333;
    background-color: var(--nav-color);
}

nav > div {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

nav > div >span {
  padding: 0.5rem 1rem;
  margin: 0 0.7rem;
  cursor: pointer;
}

nav > div > span:hover{
  box-shadow: 0 0 5px #444;
  opacity: 0.7;
}


#logo {
  font-size: 24px;
  padding: 0.5rem 1rem;
  color: var(--font-color);
  font-family: var(--logo-font);
}

#logo:hover{
  box-shadow: 0 0 5px #444;
  opacity: 0.7;
}

  
#cart-container {
    background-color: #bbb;
    height: 2em;
    border-radius: 15px;
    width: 100px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    cursor: pointer;
    margin-right: 2em;
}
  
#cart-container:hover {
    background-color: var(--btn-color);
}
  
#cart-count {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: white;
    text-align: center;
}


footer {
  position: fixed;
  right:0;
  left:0;
  bottom:0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 0 10px; 
  -webkit-user-select: none; 
      -ms-user-select: none; 
          user-select: none;
  box-shadow: 0 -2px 3px #333;
  background-color: var(--nav-color);
  padding: 0.5em;
}

footer > p {
  padding: 0 30px;
}
footer > a,
footer > a:visited {
  color: var(--link-color);
}

#menu {
  width: 30px;
  height: 20px;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

#menu > div {
  background-color: var(--font-color);
  width: 100%;
  height: 3px;
}

#menu:hover > div{
  background-color: var(--link-color);
}

@media (max-width: 600px) {
  #menu {
    display: flex;
  }

  #nav-links {
    display: none;
  }
}

#user {
  display: flex;
  justify-content: space-around;
  font-size: 1.3rem;
}
#user > span {
  padding: 0 0.5rem;
}


#user,
#user:visited {
  color: var(--text-color);
}
.modal {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 500px;
    height: 350px;
    max-width: 90%;
    position: fixed;
    top: 50%;
    left:50%;
    -webkit-transform: translate(-50%, -50%) scaleY(0);
            transform: translate(-50%, -50%) scaleY(0);
    opacity: 0;
    transition: 200ms ease-in-out;
    z-index:20;
    background-color: var(--modal-color);
    border-radius: 16px;
    box-shadow: 2px 2px 5px #777;
  }
  
  .modal.active {
    -webkit-transform: translate(-50%, -50%) scaleY(1);
            transform: translate(-50%, -50%) scaleY(1);
    opacity: 1;
  }
  
  .modal > p> a {
    margin : 0 1em;
  }
  
  .modal > p > a,
  .modal > p > a:visited{
    color: var(--link-color);
  }

  .form{
    padding: 1em;
  }
  
  .form-content {
    display: grid;
    grid-template-columns: auto 150px 20px;
    margin: 10px;
    align-items: center;
  }
  
  .form-content input {
    font-family:Arial, Helvetica, sans-serif;
    font-weight: 700;
  }
  
  .form-content label{
    padding: 0.3em 1em;
  }

  .visibility {
    padding: 0 0.3em;
    cursor: pointer;
    color: var(--link-color);
  }
  
  .auth-message{
    color: var(--danger-color);
    margin-top: 0.5rem;
  }
  .modal-switch{
    color: var(--link-color);
    cursor: pointer;
  }
  #cart-preview{
    background-color: var(--cart-preview-color);
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    box-shadow: -5px 0 5px #777;
    transition: 200ms ease-in-out;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 1em;
  }

  
  .cart-preview-active{
    left: calc(100% - 350px);
  }
  
  .cart-preview-hidden{
    left: 120%;
  }

  .checkout-btn {
    position: absolute;
    bottom: 10px;
    margin: 1em 0;

    width: 80%;
    text-align: center;
  }


.book-preview-wrapper{
  margin: 1em 0;
  width: 90%;
  max-height: calc(100vh - 140px);
  overflow: auto;
}

.checkout-btn > span{
  color: white;
  padding: 0 0.5em;
}

@media (max-width: 600px) {
  #cart-preview {
    display: none;
  }
}
.book-preview {
    display: grid;
    grid-template-columns: auto 3fr 1fr;
    grid-gap: 1em;
    justify-content: center;
    align-items: center;
}

.preview-img {
    grid-area: 1/1/4/2;
}

.preview-info {
    height: 100%;
    grid-area: 1/2/4/3;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.preview-info > p {
    max-width: 110px;
    word-wrap: break-word;
}


.preview-img > img {
    height: 80px;
}

.preview-delete {
    grid-area: 1/3/4/4;
    display: flex;
    align-items: center;
    justify-content: center;
}
#side-drawer{
    position: fixed;
    width: 350px;
    top: 0;
    bottom: 0;
    background-color: white;
    z-index: 10;
    box-shadow: 5px 0 5px #777;
    display: grid;
    justify-content: center;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto) 1fr;
    text-align: center;
    transition: 200ms ease-in-out;
}

#side-drawer > h2 {
    padding: 1rem 0;
}

#side-drawer > div:not(:last-child) {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 3rem;
    font-size: 1.2rem;
}

#side-drawer > div:not(:last-child):hover {
    background-color: var(--nav-color);
}

.side-drawer-active{
    right: calc(100% - 350px);
}

.side-drawer-hidden{
    right: 120%;
}

@media (min-width: 600px){
    #side-drawer{
        display: none;
    }
}

#side-drawer-cart-container > span {
    margin-left: 0.3rem;
    
}

#side-drawer-cart-count {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: var(--font-color);
    color: white;
}

#side-drawer-cart-preview{
    border-top: 1px solid;
    padding-top: 1rem;
    display: flex;
    flex-direction: column;
    width: 100%;
    max-height: calc(100% - 80px);
    height: calc(100vh - 160px);
    justify-content: space-between;
    align-items: center;
    overflow: auto; 
}

h2 > a,
h2 > a:visited {
    color: var(--text-color);
    cursor: pointer;
}
.cart-screen {
    display: flex;
}

.cart-screen-left{
    flex: 0.7 1;
    margin-right: 3em;
    margin-bottom: 1.5em;
}

.cart-screen-left > div {
    border-bottom: 1px solid #333;
}

.cart-screen-right{
    flex: 0.3 1;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    box-shadow: 2px 2px 5px #444;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 10px;
}

.cart-screen-info {
    width: 80%;
    text-align: center;
    font-size: 1.6rem;
}

.cart-screen-info > p {
    border-bottom: 1px solid #333;
    padding: 0.8rem;
}

.cart-screen-right > * {
    margin: 0.8em;
}

.reset-cart-btn {
    background-color: var(--danger-color);
    display: flex;
    justify-content: center;
    align-items: center;
}

.reset-cart-btn > * {
    margin:  0 0.8em;
}

h3 {
    margin: 1rem;
    text-align: center;
}

@media (max-width: 600px) {
    .cart-screen{
        flex-direction: column;
    }

    .cart-screen-left{
        margin-right: 0;
    }
}
.cart-item{
    display: grid;
    grid-template-columns: auto 3fr 1fr 1fr 1fr;
    grid-gap: 0.3rem 1rem;
    padding: 0.5rem;
    place-items: center;
    background-color: white;
    margin: 0.8rem;
    border-radius: 8px;
    height: 96px;
}

.cart-item-img{
    grid-area: 1/1/2/2;
}

.cart-item-img > img {
    height:80px;
}

.cart-item-name {
    color: var(--text-color);
    font-size: 1.2rem;
}

.cart-item-name:hover{
    color: var(--btn-color);
}

.cart-item-delete{
    cursor: pointer;
    transition: all 200ms ease-in-out;
}

.cart-item-delete:hover,
.cart-item-delete:active,
.cart-item-delete:focus
{
    color: var(--danger-color);
    -webkit-transform: scale(1.2);
            transform: scale(1.2)
}
#all-books {
    display: grid;
    grid-gap: 1em;;
    grid-template-columns: repeat(auto-fit, minmax(350px ,1fr));
}

.book {
    box-shadow: 2px 2px 5px #555;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 0.8em;
    width: 350px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-top: 10px auto;
}

.book-info > p {
    width: 300px;
    margin: 0.5rem 24px;
}

.info-name {
    font-size: 1.2em;
    display:flex;
    justify-content: space-between;
}



.book img {
    margin: 20px;
}


.follow-icon,
.cart-icon {
    cursor: pointer;
    color: white;
    margin-left: 0.5rem
}

.follow-icon:hover,
.cart-icon:hover {
    color: var(--follow-btn-color);
}
.followed,
.in-cart {
    color: var(--follow-btn-color);
}

.purchased,
.purchased:hover {
    color: #666;
    cursor:default;
}



.book-screen {
    display: flex;
}

.book-screen > div{
    padding: 0 0.5rem;
}

.book-screen-left {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex: 0.8 1;
}

.left-img {
    display: flex;
    justify-content: center;
    margin: 1rem;
}

.left-info {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.left-info > p {
    padding: 0.3rem;
}

.left-info > p:not(:last-child) {
    border-bottom: 1px solid #999;
}

.book-screen-right{
    margin-top: 150px;
    display: flex;
    flex: 0.2 1;
    flex-direction: column;
    align-items: center;
}

.right-buttons > div{
    margin: 1rem;
    width: 120px;
}

.follow-btn {
    background-color: var(--follow-btn-color);
}

.in-cart-btn,
.followed-btn {
    background-color: #888;
}


.purchased-btn,
.purchased-btn:hover {
    background-color: #888;
    cursor: default;
    opacity: 1;
    box-shadow:none;
}

@media (max-width:600px) {
    .book-screen {
        flex-direction: column;
    }

    .book-screen-right{
        margin-top: 50px;
    }
}
#back-to-shop, #back-to-shop:visited {
    color: var(--link-color);
  }

.success-screen {
  text-align: center;
}

.success-screen > * {
  margin: 2rem;
  font-size: 1.5em;
}
.purchase-screen {
    text-align: center;
    border-bottom:  1px solid #555;
}

.purchase-screen > h2 {
    margin: 2rem;
}
.purchased-view {
    display: grid;
    grid-template-columns: auto 4fr 1fr;
    grid-gap: 0.3rem 1rem;
    padding: 0.5rem;
    place-items: center;
    background-color: white;
    margin: 0.8rem;
    border-radius: 8px;
    height: 96px;
    width: 90vw;
}
.expires {
    margin: 0.5rem;
}

.expires-urgent{
    color: var(--danger-color);
}

.purchased-view > img {
    height: 80px;
}

.purchased-book-info{
    margin-top: 0.3rem;
}

.purchased-book-info > span {
    margin: 1rem;
}

.read-btn {
    margin-right: 1rem;
    border-radius: 10px;
}
