
nav {
    margin:0 auto;
    padding: 0.5em;
    position: fixed;
    right: 0;
    left: 0;
    top: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 0 2px 3px #333;
    background-color: var(--nav-color);
}

nav > div {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

nav > div >span {
  padding: 0.5rem 1rem;
  margin: 0 0.7rem;
  cursor: pointer;
}

nav > div > span:hover{
  box-shadow: 0 0 5px #444;
  opacity: 0.7;
}


#logo {
  font-size: 24px;
  padding: 0.5rem 1rem;
  color: var(--font-color);
  font-family: var(--logo-font);
}

#logo:hover{
  box-shadow: 0 0 5px #444;
  opacity: 0.7;
}

  
#cart-container {
    background-color: #bbb;
    height: 2em;
    border-radius: 15px;
    width: 100px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    cursor: pointer;
    margin-right: 2em;
}
  
#cart-container:hover {
    background-color: var(--btn-color);
}
  
#cart-count {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: white;
    text-align: center;
}


footer {
  position: fixed;
  right:0;
  left:0;
  bottom:0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 0 10px; 
  user-select: none;
  box-shadow: 0 -2px 3px #333;
  background-color: var(--nav-color);
  padding: 0.5em;
}

footer > p {
  padding: 0 30px;
}
footer > a,
footer > a:visited {
  color: var(--link-color);
}

#menu {
  width: 30px;
  height: 20px;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

#menu > div {
  background-color: var(--font-color);
  width: 100%;
  height: 3px;
}

#menu:hover > div{
  background-color: var(--link-color);
}

@media (max-width: 600px) {
  #menu {
    display: flex;
  }

  #nav-links {
    display: none;
  }
}

#user {
  display: flex;
  justify-content: space-around;
  font-size: 1.3rem;
}
#user > span {
  padding: 0 0.5rem;
}


#user,
#user:visited {
  color: var(--text-color);
}